'use client';

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Mail, Phone, MapPin, Volume2 } from "lucide-react";
import { useSpeech } from "@/hooks/use-speech";
import { useLanguage } from "@/contexts/language-context";

export default function ContactsPage() {
    const { speak, isSpeaking } = useSpeech();
    const { language } = useLanguage();

    const handleSpeak = () => {
        const textToSpeak = `
            Get in Touch. We'd love to hear from you. Please fill out the form or use our contact details.
            Name. Email. Message.
            Contact Information.
            Email: contact@mannova.com.
            Phone: +91 123 456 7890.
            Address: 123 AgriTech Park, Bengaluru, India.
        `;
        speak({ text: textToSpeak, lang: language });
    };

  return (
    <div className="grid gap-6 md:grid-cols-2">
      <Card>
        <CardHeader>
           <div className="flex justify-between items-center">
            <div>
              <CardTitle>Get in Touch</CardTitle>
              <CardDescription>We'd love to hear from you. Please fill out the form or use our contact details.</CardDescription>
            </div>
             <Button variant="outline" size="icon" onClick={handleSpeak} disabled={isSpeaking}>
              <Volume2 className="h-5 w-5" />
              <span className="sr-only">Read page content</span>
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="name">Name</label>
            <Input id="name" placeholder="Your Name" />
          </div>
          <div className="space-y-2">
            <label htmlFor="email">Email</label>
            <Input id="email" type="email" placeholder="your.email@example.com" />
          </div>
          <div className="space-y-2">
            <label htmlFor="message">Message</label>
            <Textarea id="message" placeholder="Your message..." />
          </div>
          <Button>Send Message</Button>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Contact Information</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center gap-4">
            <Mail className="h-5 w-5 text-primary" />
            <span>contact@mannova.com</span>
          </div>
          <div className="flex items-center gap-4">
            <Phone className="h-5 w-5 text-primary" />
            <span>+91 123 456 7890</span>
          </div>
          <div className="flex items-center gap-4">
            <MapPin className="h-5 w-5 text-primary" />
            <span>123 AgriTech Park, Bengaluru, India</span>
          </div>
          <div className="w-full h-64 bg-muted rounded-lg mt-4">
            {/* Placeholder for a map */}
            <div className="w-full h-full flex items-center justify-center text-muted-foreground">Map Placeholder</div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
